















import Vue from 'vue';
import getRepository from '@/utilities/getRepository';
import { MoviesState } from '@/views/types';

export default Vue.extend({
  name: 'Movies',
  data() {
    return {
      error: false,
      fetching: false,
      maxPages: 0,
      movies: [],
    } as MoviesState;
  },
  props: {
    filtered_by: {
      type: String,
      required: true,
    },
  },
  components: {
    MoviesItem: () => import('@/components/MoviesItem.vue'),
    MoviesButtons: () => import('@/views/movies/MoviesButtons.vue'),
    MoviesErrorHandler: () => import('@/views/movies/MoviesErrorHandler.vue'),
  },

  methods: {
    async getMovies() {
      this.error = false;
      this.fetching = true;
      const { page, query } = this.$route.query;

      try {
        const {
          data: { results, total_pages: totalPages },
        } = await getRepository(this.filtered_by, page, query);
        this.maxPages = totalPages;
        this.movies = results;
        this.fetching = false;
      } catch (error) {
        this.error = true;
      }
    },
  },
  watch: {
    '$route.fullPath': {
      handler() {
        this.getMovies();
      },
    },
  },
  created() {
    this.getMovies();
    document.title = 'Movie Maniac';
  },
});
